import Nav from "./Nav";
import Section from "../Section/Section";

import {
    BrowserRouter as Router,
    Switch,
    Route,
} from 'react-router-dom';

const Rout = () => {
    return (
        <div>


            <Router>
                <Switch>
                    <Route path="/movies" id="movies">
                        <h1>Movie list</h1>
                    </Route>
                    <Route path="/" exact>
                        <Nav />
                        <Section />
                    </Route>
                    <Route path="/users">
                        <Nav />
                    </Route>
                </Switch>
            </Router>
        </div>
    )
}
export default Rout;

